<script>
export default {
  name: 'MuscleMap',
  data() {
    return {
      selectedPart : null
    };
  },
  methods: {
    selectPart(part) {
      this.selectedPart  = part;
      this.$router.push(`/home/muscle/${part}`);
    }
  }

};
